import { Component } from 'react'
//import './main-bandeau.scss'

/**
 * Send Dom bandeau
 * @return { Dom }
 */
class Bandeau extends Component {
    constructor(props) {
        super(props);
        this.state = {
            page: this.props.page,
            texte: this.props.text,
            image: this.props.img,
            alt: this.props.alt,
            filter: this.getClassFilter(this.props.page)
        }
    }

    getClassFilter(page) {
        return page === "Describ" ? "noFilter" : "filter"
    }

    render() {
        return (
            <picture className="bandeau-content">
                <img className={`bandeau-content__img  ${this.state.filter}`} src={this.state.image} alt={this.state.alt} />
                <h1 className="bandeau-content__text">{this.state.texte}</h1>
            </picture>
        )
    }
}

export default Bandeau;
