import { Component } from "react";
//import './main-slideshow.scss';


class SlideShow extends Component {
    constructor(props) {
        super(props);

        this.state = {
            image: this.props.images[0],
            currentIndex: 0,
            alt: "slideShow de l'interieur",
            animateTimer: '',
            touchStart:0,
            posEnd:0
        }
    }

    componentDidMount=()=> {
        this.startAnimation();
    }

    componentWillUnmount=()=> {
        this.stopAnimation();
    }

    startAnimation =()=> {
        this.animate = setInterval(() => {
            this.toShowSlide("rigth")
        }, 3000);
        this.setState({ animateTimer: this.animate })
    }

    stopAnimation =()=> {
        clearInterval(this.state.animateTimer);
    }

    handleButtonClick =(e)=> {
        e.preventDefault();
        let pos = e.target.dataset.position;
        this.toShowSlide(pos)
    }
/* 
     handleTouchStart=()=> {
        this.setTouchStart(e.targetTouches[0].clientX);
    }
    
    function handleTouchMove(e) {
        setTouchEnd(e.targetTouches[0].clientX);
    }
    
    function handleTouchEnd() {
        if (touchStart - touchEnd > 150) {
            // do your stuff here for left swipe
            moveSliderRight();
        }
    
        if (touchStart - touchEnd < -150) {
            // do your stuff here for right swipe
            moveSliderLeft();
        }
    }

    ontouchcancel: The event occurs when the touch is interrupted
ontouchend: The event occurs when a finger is removed from a touch screen
ontouchmove: The event occurs when a finger is dragged across the screen
ontouchstart: The event occurs when a finger is placed on a touch screen
 */
    toShowSlide = (direction) => {
        const size = this.props.images.length;
        let currentIndex = this.state.currentIndex;
        let newIndex = (direction === "left" ? --currentIndex : ++currentIndex);
        if (newIndex === size) {
            newIndex = 0;
        }
        else if (newIndex < 0) {
            newIndex = (size - 1);
        }
        this.setState(() => {
            return {
                image: this.props.images[newIndex],
                currentIndex: newIndex
            }
        })
    }

    render() {
        return (
            <div className="content-slideShow" onMouseEnter={this.stopAnimation} onMouseLeave={this.startAnimation} >
                <img className="slideShow-img" src={this.state.image} alt={this.state.alt} />
                <span className="slideShow-arrow left" aria-label="image prédente" data-position="left" onClick={this.handleButtonClick}></span>
                <span className="slideShow-arrow right" aria-label="image suivante" data-position="right" onClick={this.handleButtonClick}></span>
                <span className="slideShow-number">{this.state.currentIndex+1}/{this.props.images.length}</span>
            </div>

        )
    }

}
export default SlideShow
